.gpt3__cta{
    display: flex;
    flex-direction: row;
    padding: 2rem;
    justify-content:space-between;
    align-items: center;
    background: var(--gradient-bar);
    border-radius: 10.7236px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 1);
    border: 1px solid black;
}

.gpt3__cta-content{
    flex: 1;
}
.gpt3__cta-content p{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 12px;
    line-height: 30px;

    margin-bottom: 1rem ;


    color: #0E0E0E;

}
.gpt3__cta-content h3{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 24px;
    line-height: 45px;

    margin-bottom: 1rem ;


    color: #0E0E0E;

}
.gpt3__cta-button{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 2rem;
}
.gpt3__cta-button button{
    background: #000000;
    border-radius: 40px;
    color: #fff;
    padding: 0.5rem 1rem;
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 18px;
    line-height: 30px;
    border: none;
    outline: none;
    cursor: pointer;
    min-width: 150px;

}
@media screen and(max-width:650px) {

    .gpt3__cta{
        flex-direction: column;
    }
    .gpt3__cta-button{
        margin: 2rem 0 0;
    }
    
}
@media screen and(max-width:550px){
    .gpt3__cta{
        margin: 4rem 2rem;
    }
    .gpt3__cta-content h3{
        font-size: 18px;
        line-height: 32px;

    }
    .gpt3__cta-button button{
        font-size: 14px;
        line-height: 28px;

    }
}