    font-weight: bold;
    font-size: 11.649px;
    line-height: 35px;
    color: #fff;
}
.gpt3__blog-container_article-content h3{

    font-family: var(--font-family);
    font-weight: 800;
    font-size: 25.11px;
    line-height: 30px;
