}
.gpt3__cta{
    display: flex;
    flex-direction: row;
    padding: 2rem;
    justify-content:space-between;
    align-items: center;
    background: var(--gradient-bar);
    border-radius: 10.7236px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 1);
    border: 1px solid black;
}

.gpt3__cta-content{
    flex: 1 1;
}
.gpt3__cta-content p{
    font-family: var(--font-family);
    font-weight: 500;
